<template>
    <div class="sub_index_2_wrap">
        <div class="sub_index_2_title">{{ title }}</div>
        <div class="sub_index_2_content">{{ content }}</div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                title: "",
                content: ""
            }
        },
        mounted() {
            if (!this.$route.params.id || (isNaN(parseInt(this.$route.params.id)) && this.$route.params.id != "default")) {
                this.$router.push("/index/sub_index_2/default");
                this.title = "2";
                this.content = "2sub_index_2default";
            } else if (this.$route.params.id == "default") {
                this.title = "2";
                this.content = "2sub_index_2default";
            } else {
                this.title = "2";
                this.content = `id${this.$route.params.id}`;
            }
        }
    }
</script>

<style lang="less">
    .sub_index_2_wrap {
        display: flex;
        position: relative;
        flex-direction: column;

        .sub_index_2_title {
            font-size: 16px;
            margin-bottom: 10px;
        }

        .sub_index_2_content {
            font-size: 14px;
        }
    }
</style>